.Drawer{
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 80%;
  max-width: 300px;
  padding: 20px 10px;
  box-sizing: border-box;
  background: #fff;
  z-index: 90;
  transform: translateX(0px);
  transition: all 0.22s ease-in;

  &.close{
    transform: translateX(-300px);
  }

  ul{
    list-style: none;
    margin: 0;
    padding: 0;

    li{
      margin-bottom: 15px;

      a{
        color: #363f54;
        font-size: 30px;
        text-decoration: none;
        background-color: #fff;
        position: relative;
        padding: 0 20px 10px 20px;
        transition: opacity 0.3s;


        &:hover,
        &.active{
          opacity: 0.7;
        }
      }
    }

  }
}